import {
	getUserAuth,
	auth2Page
} from '../../../utils/login.js'
import {
	showPic,
	toast,
	navTo,
	formateTime
} from '../../../utils/default.js'
import {
	uploadLog
} from '../../../api/api.js'
export default {
	data() {
		return {
			userAuth: false,
			userInfo: null,
			exercise: {
				clock: null,
				clockState: false,
				start: '',
				end: '',
				duration: '00:00:00',
			},
			lessTime: 1
		}
	},
	methods: {
		showPic,
		navTo,
		getUserAuth(e) {
			if (getUserAuth.call(this, e)) {
				this.userAuth = true;
				this.userInfo = getApp().globalData.userInfo;
			}
		},
		clockChange(history = false) {
			if (!history) this.exercise.clockState = !this.exercise.clockState;
			if (this.exercise.clockState) {
				// console.log(this.exercise);
				this.exercise.end = '';
				this.exercise.clockState = true;
				this.exercise.uploaded = false;
				let tick = new Date(0, 0, 0, 0, 0, 1);
				if (history) {
					tick.setSeconds((new Date().getTime() - new Date(this.exercise.start).getTime()) / 1000)
				} else {
					this.exercise.start = formateTime(new Date());
				}
				this.exercise.duration = formateTime(tick)
				this.exercise.clock = setInterval(() => {
					tick.setSeconds(tick.getSeconds() + 1);
					// console.log(1);
					this.exercise.duration = formateTime(tick);
					if (tick.getHours() > 23) {
						this.clockChange();
					}
				}, 1000)
			} else {
				if (!history) {
					this.exercise.end = formateTime(new Date());
				}
				this.exercise.clockState = false;
				clearInterval(this.exercise.clock);
				this.updateLog();
			}
		},
		updateLog() {
			if (this.exercise.uploaded) return;
			let duration = new Date(this.exercise.end).getTime() - new Date(this.exercise.start).getTime();
			// console.log(duration);
			if (duration < this.lessTime) {
				toast({
					title: `练习时间少于${this.lessTime/1000/60}分钟不计入哦，请加油~`,
					duration: 3000
				})
				return
			}
			uploadLog({
					data: {
						start: this.exercise.start,
						end: this.exercise.end,
						// duration,
					},
					wxCloud: true
				})
				.then(r => {
					this.exercise.uploaded = true;
					uni.setStorage({
						key: 'exercise',
						data: this.exercise
					})
				})
		}
	},
	onLoad() {
		console.log('Mine onLoad');
		this.$state.setWatch(this, 'userAuth', 'userAuth');
		this.$state.setWatch(this, 'lessTime', 'lessTime');
		this.userInfo = getApp().globalData.userInfo;
		getApp().loginCB = () => {
			console.log('loginCB');
			this.userInfo = getApp().globalData.userInfo;
		}
	},
	onShow() {
		let history = uni.getStorageSync('exercise')
		if (history) {
			this.exercise = history;
			this.exercise.start = formateTime(new Date(history.start));
			if (history.end) this.exercise.end = formateTime(new Date(history.end));
			this.clockChange(true);
		}
	},
	onHide() {
		clearInterval(this.exercise.clock)
		if (this.exercise.start) uni.setStorage({
			key: 'exercise',
			data: this.exercise
		})
	},
	onUnload() {
		clearInterval(this.exercise.clock)
		if (this.exercise.start) uni.setStorage({
			key: 'exercise',
			data: this.exercise
		})
	}
}
